<template>
    <div>
        <v-menu offset-y open-on-hover transition="slide-y-transition" id="dropdownMenu">
            <v-layout slot="activator" column id="user_info">
                <v-layout row>
                    <v-avatar :size="40" id="image">
                        <img src="../../assets/user-icon.png" alt="avatar" />
                    </v-avatar>
                    <v-card-text id="name">{{ user.firstName }} {{ user.lastName }}</v-card-text>
                </v-layout>
            </v-layout>
            <v-list>
                <v-list-tile @click="userProfile">
                    <v-list-tile-title>{{ myProfile }}</v-list-tile-title>
                </v-list-tile>
                <v-list-tile v-show="!isOnFloorMap" @click="$router.push('/floor-map')">
                    <v-list-tile-title>{{ myHome }}</v-list-tile-title>
                </v-list-tile>
                <v-list-tile @click="logOut">
                    <v-list-tile-title>{{ signOut }}</v-list-tile-title>
                </v-list-tile>
            </v-list>
        </v-menu>
    </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import router from '@/router'
export default {
    name: 'UserInfo',
    data: () => ({
        ...mapGetters('account', ['user']),
        items: ['Log out'],
        signOut: 'Log out',
        myProfile: 'My Profile',
        myHome: 'Home',
        isOnFloorMap: false
    }),
    created() {
        this.isOnFloorMap = this.$route.name === 'employee-assets' ? true : false
    },
    methods: {
        ...mapActions('account', ['logout']),
        ...mapActions('toolbar', ['hideAdminToolbar']),
        logOut() {
            this.logout()
            router.push('/')
        },
        userProfile() {
            this.hideAdminToolbar()
            router.push({ path: 'myprofile' })
        }
    }
}
</script>

<style>
#dropdownMenu {
    width: 120%;
}
#name {
    font-family: Roboto;
    font-size: 15px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
}
#image {
    position: absolute;
    right: 19px;
    top: 12px;
}
#user_info {
    margin-right: 35px;
}
@media only screen and (max-width: 500px) {
    #name {
        display: none;
    }
}
</style>

